/**
 * Collection Template Script
 * ------------------------------------------------------------------------------
 * A file that contains scripts highly couple code to the Collection templates.
 *
   * @namespace collection
 */

//import $ from 'jquery';
import {register} from '@shopify/theme-sections';

//import Ajaxinate for infinite scrolling with Ajax
import '../vendor/ajaxinate.min.js';

const selectors = {
  sortBy: '[data-sort-by]',
  sortByDefault: '[data-sort-by-default]',
  swatchContainer: '[data-product-item-swatches]',
  infinite: '[data-infinite]'
};

/**
 * Collection section constructor. Runs on page load as well as Theme Editor
 * `section:load` events.
 * @param {string} container - selector for the section container DOM element
 */

register('collection', {
  onLoad() {
    this.$container = $(this.container);
    this.init_sort_by();    
    this.init_collection_swatches();
    this.init_collection_lazyscroll();
  },
  /**
   * Setup sort by select and the change callback
   */
  init_sort_by() {
    
    $(selectors.sortBy, this.$container)
      .val($(selectors.sortByDefault, this.$container).val())
      .bind('change', function() {
        Shopify.queryParams.sort_by = jQuery(this).val();
        location.search = jQuery.param(Shopify.queryParams);
      });
  },
  
  /**
   * Collection swatches 
   */
  init_collection_swatches() {
    
    if($(selectors.swatchContainer, this.$container).length > 0)
    {

      $('body').on('click', '.swatch span', function() {
        
        var optionIndex = $(this).closest('.swatch').attr('data-option-index');
        var optionValue = $(this).closest('.swatch').attr('data-value');
        
        /* Update Image */
        if($(this).data("image").indexOf("no-image") == -1) {
          $(this).parents('.product-grid-item').find('.image-pg img').attr('src', $(this).data("image"));
        } else {
          var productImage = $(this).closest('.swatch').data('product-image');
          $(this).parents('.product-grid-item').find('.image-pg img').attr('src', productImage);
        }
        
        /* Update Image Link */
        $(this).parents('.product-grid-item').find('.image-pg').attr('href', $(this).closest('.swatch').data("variant-link"));
        
        /* Update Active State */
        $(this).parents('.collection_swatches').find('.active').removeClass('active');
        $(this).closest('.swatch').addClass('active');
        
        var active_swatch_values = [];
        $(this).closest('.product-grid-item').find(".swatch.active").each(function() {
          active_swatch_values.push($(this).data('value'));
        });
        
        /* Update the select dropdown with chosen swatch for add to cart functionality */
        var optionId = $(this)
          .closest('.product-grid-item-form')
          .find('[data-option-selector] option[data-option1="' + active_swatch_values[0] + '"][data-option2="' + active_swatch_values[1] + '"][data-option3="' + active_swatch_values[2] + '"]').val();
          
          $(this)
          .closest('.product-grid-item-form')
          .find('[data-option-selector]')
          .val(optionId)
          .trigger('change');
      });
    }
  },
  
  init_collection_lazyscroll() {
    if($(selectors.swatchContainer, this.$container).length > 0) {
      console.log('trying to get ajaxinate call in here');
       // var endlessScroll = new Ajaxinate();
    }
  },
  
  /**
   * Event callback for Theme Editor `section:unload` event
   */
  onUnload: function() {
    this.$container.off(this.namespace);
  }
});

